import React from "react";
import {
  Content,
  Header,
  HeaderName,
} from "@carbon/react";
import { Theme } from "@carbon/react";
import "./app.scss";
import AboutMeMobile from "./gh-pages/aboutMobile";
import HomepageMobile from "./gh-pages/homeMobile";
import Navigation from "./components/Navigation";
import { Provider, useSelector } from "react-redux";
import { store } from "../src/store/Store";
// import { PersistGate } from "redux-persist/integration/react";
import { BrowserRouter, Route, Routes, Navigate, HashRouter } from "react-router-dom";
import { useMediaQuery } from 'react-responsive'
import AboutPage from "./gh-pages/about";
import HomePage from "./gh-pages/home";
import HeaderNav from "./components/HeaderNav";

function App() {

  const isDesktopOrLaptop = useMediaQuery({
    query: '(min-width: 1224px)'
  })
  const isBigScreen = useMediaQuery({ query: '(min-width: 1824px)' })
  const isTabletOrMobile = useMediaQuery({ query: '(max-width: 1224px)' })
  const isPortrait = useMediaQuery({ query: '(orientation: portrait)' })
  const isRetina = useMediaQuery({ query: '(min-resolution: 2dppx)' })


  return (
    // <div className="bg-gray-300vh] w-100">
    <HashRouter>


      {/* <Provider store={store}> */}

      <div className="bg-blue-300 h-full w-full">
        {/* <Content> */}
        <Theme theme="g100">
          <Header aria-label="App">

            {(isBigScreen || isDesktopOrLaptop) ? (
              <HeaderName
                href="/gh-pages/#"
                prefix="Homoapatrol"
              >
                <p>Panda</p>
              </HeaderName>
            ) : (
              <>
                <HeaderName
                  href="/gh-pages/#"
                  prefix="Homoapatrol"
                >
                  Panda
                </HeaderName>
                <HeaderNav />

              </>
            )}

          </Header>
        </Theme>
        {(isBigScreen || isDesktopOrLaptop) ? (
          <Navigation />) : (<></>)
        }
      </div>
      {/* <Theme theme="g10"> */}

      <Routes>
        {(isBigScreen || isDesktopOrLaptop) ? (
          <>
            <Route path="/" element={<Navigate to="/homepage" />} />
            <Route path="/about" element={<AboutPage />} />
            <Route path="/gh-pages/#/upload" />
            <Route path="/strongman" element={<Navigate to="https://strengthresults.com/statistics/profiles/9c41-bde0-45a7-aadf-e8d17b931149" />} />
            <Route path="/homepage" element={<HomePage />} />
          </>
        ) : (
          <>
            <Route path="/" element={<Navigate to="/homepage" />} />
            <Route path="/about" element={<AboutMeMobile />} />
            <Route path="/gh-pages/#/upload" />
            <Route path="/strongman" element={<Navigate to="https://strengthresults.com/statistics/profiles/9c41-bde0-45a7-aadf-e8d17b931149" />} />
            <Route path="/homepage" element={<HomepageMobile />} />
          </>
        )
        }



      </Routes>

      {/* </Theme> */}

      {/* </Content> */}


      {/* </Provider> */}


    </HashRouter >
    //  </div> 
  );
}

export default App;